@import url(https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,300;0,400;0,700;1,300;1,400;1,700&display=swap);
*,*::before,*::after{box-sizing:border-box;font-family:'Montserrat', sans-serif;font-size:16px;margin:0;padding:0;border:0;outline:0;font-size:100%;vertical-align:baseline;background:transparent}body{line-height:1}article,aside,details,figcaption,figure,footer,header,hgroup,menu,nav,section{display:block}ul{list-style:none}a{margin:0;padding:0;font-size:100%;vertical-align:baseline;background:transparent;cursor:pointer}button{background-color:transparent;color:inherit;border-width:0;padding:0;cursor:pointer}body{background-color:#FFFEEB;color:#2A2927}h1,h2{text-transform:capitalize;margin-bottom:1rem}h1{font-size:2rem}h2{font-size:1.5rem}button{outline:none;border:none;border-radius:4px}#root{display:grid;grid-template-rows:-webkit-max-content auto -webkit-max-content;grid-template-rows:max-content auto max-content;min-height:100vh}.header{padding:1rem 0;z-index:1000;background-color:#ecebd9}.header .header__container{display:flex;align-items:center;justify-content:space-between;max-width:1140px;padding:0 1rem;margin:0 auto}.header .header__title{margin:0}.header .header__btn{padding:.8rem 1.2rem;color:#FFFEEB;background:linear-gradient(135deg, #9785E0, #85A9E0)}.header__img{width:3rem}.main__container{display:grid;grid-gap:1rem;gap:1rem;grid-template-columns:1fr;padding:2rem 1rem;max-width:1140px;width:100%;margin:0 auto}.counter{padding:1rem;background-color:#2A2927;color:#FFFEEB;text-align:center;border-radius:10px}.article{display:flex;align-items:center;justify-content:space-between;padding:1rem;border:1px solid #2A2927;border-radius:10px;background-color:#FFFEEB}.article__details{display:flex}.article__img{border-radius:3rem;object-fit:cover;object-position:center;width:3rem;height:3rem}.article__content{margin:0 1rem}.article__title{font-size:1.5rem;margin-bottom:.5rem}.article__subtitle{font-size:.8rem}.article__rates>.article__subtitle{font-size:1rem;color:#2A2927}.footer{text-align:center;padding:1rem 0;background-color:#ecebd9;color:#2A2927}

.switch{position:relative;width:65px;display:inline-block;padding:0}.switch-checkbox{display:none}.switch-label{display:block;overflow:hidden;cursor:pointer;border-radius:20px;margin:0}.switch-inner{display:block;width:200%;margin-left:-100%;transition:margin 0.3s ease-in 0s}.switch-inner:before,.switch-inner:after{display:block;float:left;width:50%;height:1.9rem;padding:0;line-height:2rem;font-size:1.6rem}.switch-inner:before{content:"ðŸŒœ";text-transform:uppercase;padding-left:.3rem;text-align:left;background-color:#1a1917}.switch-inner:after{content:"ðŸŒž";text-transform:uppercase;padding-right:.2rem;text-align:right;background-color:#2A2927}.switch-switch{display:block;width:25px;margin:3px;background:#FFFEEB;position:absolute;top:0;bottom:0;right:2.1rem;border-radius:1.2rem;transition:all 0.3s ease-in 0s}.switch-checkbox:checked+.switch-label .switch-inner{margin-left:0}.switch-checkbox:checked+.switch-label .switch-switch{right:0px}

